body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

ul, p {
  margin: 0;
  padding: 0;
}

.list {
  list-style: none;
}

button {
  cursor: pointer;
  color: #fafafa;
  background-color: #757578;
  border: none;
  border-radius: 10px;
  padding: 5px 25px;
  font-size: 15px;
}

.search-form {
  text-align: center;
  height: 50px;
  background-color: #49bdcd;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
  display: flex;
}

.search-btn {
  margin-left: 10px;
  font-weight: 100;
}

.search-input {
  height: 25px;
  border: 1px solid rgba(255, 255, 255, .3);
  border-radius: 10px;
  padding: 0 15px;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.load-more {
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.photo-card {
  height: 195px;
  border-radius: 0 0 4px 4px;
  transition-duration: .5s;
  display: flex;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.photo-card:hover {
  transform: scale(1.25);
}

.link {
  color: inherit;
  width: 100%;
  height: 100%;
  flex-direction: column;
  text-decoration: none;
  display: flex;
}

img {
  display: block;
}

.container {
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-bottom: 30px;
  display: flex;
}

b {
  margin-bottom: 5px;
}

.info {
  background-color: #fafafa;
  justify-content: center;
  padding: 10px 0;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  margin-right: 5px;
  font-size: 10px;
  display: flex;
}

b {
  font-size: 10px;
}

